@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;900&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}
.login {
  height: 100vh;
  width: 100%;
  background: #292929;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.login__title {
  color: #fff;
  font-size: 90px;
  letter-spacing: 10px;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.login__box {
  width: 400px;
  min-width: 350px;
  background: #fff;
  border-radius: 0;
  padding: 40px;
}

.login__input {
  margin-bottom: 15px;
  position: relative;
  width: 100%;
}

.login__input input {
  height: 50px;
  width: 100%;
  font-size: 18px;
  border: 1px solid #e3e3e3;
  padding: 0;
  border-radius: 3px;
  text-indent: 10px;
  font-weight: 300;
  font-family: inherit;
  outline: none;
}
.login__input__error{
    border-color: red;
}
.login__button {
  width: 100%;
  background-color: #0374ff;
  border: 0;
  font-size: 20px;
  border-radius: 3px;
  padding: 10px;
  color: #fff;
  font-family: inherit;
  outline: none;
  cursor: pointer;
}
.login__forget__password__link {
  text-align: center;
  width: 100%;
  display: block;
  margin-top: 25px;
  color: #888888;
  font-size: 12px;
}
.login__copyrights {
  color: #fff;
  font-size: 14px;
}
.login__signup {
  margin-top: 40px;
  margin-bottom: 5px;
  color: #fff;
  font-size: 17px;
}

.navbar {
  background: #292929;
  color: #fff;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 1px 1px 8px 2px #00000045;
}

.navbar__signout {
  background-color: indianred;
  color: #fff;
  border: none;
  cursor: pointer;
  padding: 8px 10px;
  border-radius: 3px;
  letter-spacing: 1px;
}

.dashboard{
    padding: 50px 3rem;
    background: #f5f5f5;
    width: 80%;
    min-height: 100vh;
}
.dashboard__charts{
    display: flex;
    align-content: space-around;
    flex-wrap: wrap;
}
.chart__container{
    background: #fff;
    padding: 15px;
    width: 46%;
    height: 300px;
    border-radius: 5px;
    box-shadow: 2px 0px 3px #00000029;
    position: relative;
    margin: 0 auto;
    margin-bottom: 15px;
}

.body__wrapper{
    display: flex;
}

.sidebar{
    width: 20%;
    min-width: 250px;
    background: #313131;
}


.dashboard__table{
    margin-top: 40px;
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 2px 0px 3px #00000029;
}

.dashboard__table table{
    text-align: left;
    width: 100%;
}
.dashboard__table table th{
    border-bottom: 1px solid #e0e0e0;
    padding: 10px 10px 15px 10px;
    font-size: 14px;
    border-collapse: collapse;
}

.dashboard__table table tr{
    transition: .1s background ease-in-out;
}

.dashboard__table table tr:hover{
    background: #e0e0e0;
    cursor: pointer;
}


.dashboard__table table td{
    padding: 20px 10px 15px 10px;
    border: none;
    font-size: 13px;
    font-weight: 400;
    border: 0 solid transparent;
    border-collapse: collapse;
}

.dashboard__table table tr:not(:last-child) td{
    border-bottom: 1px solid #e0e0e0;
}
